import React, { Component } from 'react'
import { Spinner } from './Spinner';
import Newsitem from './Newsitem'
import PropTypes from 'prop-types'
import InfiniteScroll from "react-infinite-scroll-component";


export class News extends Component {
    static defaultProps = {
        country: 'in',
        category: "general",
    }
    static propTypes = {
        country: PropTypes.string,
        category: PropTypes.string
    }
    capitalize(str) {
        return str.charAt(0).toUpperCase() + str.slice(1);
    }
    articles = []
    constructor(props) {
        super(props);
        this.state = {
            articles: this.articles,
            page: 1,
            loading: false,
        }
        document.title = `${this.capitalize(this.props.category)} - DailyNews`
    }
    // async updateNews() {
    //     let Url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=6196c1a4b19e4fcb977635a889901ae4&page=1&pageSize=8`;
    //     this.setState({ loading: false });
    //     let data = await fetch(Url);
    //     // console.log(data)
    //     let parsedData = await data.json()
    //     console.log(parsedData);
    //     this.setState({ articles: parsedData.articles, loading: false, 
    //         totalResults: parsedData.totalResults });

    // }
    async componentDidMount() {
        this.fetchMoreData()

    }

    fetchMoreData = async () => {
        this.setState({ page: this.state.page + 1 })
        let Url = `https://saurav.tech/NewsAPI/top-headlines/category/${this.props.category}/${this.props.country}.json`;
        this.setState({ loading: false });
        let data = await fetch(Url);
        let parsedData = await data.json()
        this.setState({ articles: this.state.articles.concat(parsedData.articles),
            loading: false, 
            totalResults: parsedData.totalResults });
    }

    render() {
        return (<>
            
                <h2 className="container text-center" style={{marginTop:"90px"}}> Daily News- Top HeadLines on {this.capitalize(this.props.category)}</h2>
                {/* {this.state.loading && <Spinner />} */}
                <div className="container">
                <InfiniteScroll
                    dataLength={this.state.articles.length}
                    next={this.fetchMoreData}
                    hasMore={this.state.articles.length !== this.state.totalResults}
                    loader={<Spinner />} >
                        <div className="container">
                    <div className="row my-3">
                        {this.state.articles.map((element) => {
                            return <div className="col-md-3" key={element.url}>
                                <Newsitem title={element.title ? element.title : ""} desc={element.description ? element.description : ""} url={element.urlToImage ? element.urlToImage : "https://cdn.pixabay.com/photo/2015/02/15/09/33/news-636978_960_720.jpg"} newsUrl={element.url} date={element.publishedAt} source={element.source.name?element.source.name:""} />
                            </div>
                        })}
                    </div>
                    </div>
                </InfiniteScroll>
                </div>
            

        </>)
    }
}

export default News
