import './App.css';
import React, { Component } from 'react';
import Navbar from './Navbar';
import News from './News';
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";



export default class App extends Component {
  render() {
    return (
      <div>
      <Router>
      <Navbar></Navbar>
      <Switch>
        
          <Route exact strict path="/"><News key="general" country='in' category="general"/></Route>
          <Route exact strict path="/business"><News key="business" country='in' category="business"/></Route>
          <Route exact strict path="/entertainment"><News key="entertainment" country='in' category="entertainment"/></Route>
          <Route exact strict path="/science"><News key="science" country='in' category="science"/></Route>
          <Route exact strict path="/sports"><News key="sports" country="in" category="sports"/></Route>
          <Route exact strict path="/technology"><News key="technology" country="in" category="technology"/></Route>
        </Switch>
      </Router>
      </div>
    )
  }
}