import React, { Component } from 'react'

export class Newsitem extends Component {
    render() {
        let {title, desc, url ,newsUrl, date, source} = this.props;
        return (
            <div>
                <div className="my-3"></div>
                <div className="card my-3">
                    <img src={url} className="card-img-top" alt="..."/>
                    <div className="card-body">
                    <h5 className="card-title">{title}<span className="badge rounded-pill bg-success mx-1"> {source} </span>
</h5>
                    <p className="card-text">{desc}</p>
                    <p className="card-text"><small className="text-muted">on {new Date(date).toLocaleString()}</small></p>
                    <a href={newsUrl} target="_blank, nonreferrer"  className="btn btn-primary">Go To News</a>
                    </div>
                </div>
            </div>
        )
    }
}

export default Newsitem
